/*media breckpoint mixin*/
@mixin notebook {
  @media (max-width: 1366px){
      @content;
  }
}
@mixin tablet {
  @media (max-width: 991px){
      @content;
  }
}
@mixin mobile {
  @media (max-width: 768px){
      @content;
  }
}
@mixin mobile-sm {
  @media (max-width: 576px){
      @content;
  }
}
@mixin mobile-xsm {
  @media (max-width: 400px){
      @content;
  }
}
@mixin mobile-xxsm{
  @media (max-width: 350px){
      @content;
  }
}