/*normalize css https://necolas.github.io/normalize.css/ */
@import "vendor/normalize.scss";
/*variables*/
@import "./var.scss";
/*main style*/
html{
  font-size: 16px;
  overflow-x: hidden;
}
body{
  font-family: $font;
  font-size: 1rem;
  font-weight: 300;
  -webkit-font-smoothing: antialiased;
  color: $black;
  margin: 0;
  padding: 0;
  background-color: $white;
  overflow-x: hidden;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}
p {
  margin: 0;
}
a {
  &:focus {
    outline: none;
  }
}
/*helpers class*/
@import "./help.scss";
/*mixins*/
@import "./mixin.scss";
/*=================================*/
/*block*/
/*container*/
@import "block/container.scss";
/*sidebar*/
@import "block/asside.scss";
/*footer*/
@import "block/footer.scss";
/*intro*/
@import "block/intro.scss";

